		font-size: 20rpx;
	}

	/* Âú∞ÂõæÊ®°Âºè */
	.map-box {
		width: 100%;
		height: calc(100vh - 194rpx);
		position: relative;
	}

	.map-instance {
		width: 100%;
		height: 100%;
	}

	.floating-card {
		position: absolute;
		bottom: 40rpx;
		left: 30rpx;
