		justify-content: space-between;
	}

	.f-name {
		font-size: 28rpx;
		font-weight: bold;
	}

	.f-bottom {
		display: flex;
		justify-content: space-between;
	}

	.f-score {
		font-size: 24rpx;
		color: #0086F6;
	}

	.f-price {
		font-size: 28rpx;
		color: #ff4d4f;
		font-weight: bold;
	}

	.load-more {
		text-align: center;
		padding: 40rpx;
		font-size: 24rpx;
		color: #999;
	}

	/* ‚ú®Êñ∞Â¢ûÔºöËæìÂÖ•Ê°ÜÂ∑¶‰æß‚Äú‚úï‚ÄùÂõæÊ†áÊ†∑Âºè */
	.reset-kw-btn {
		font-size: 22rpx;
		color: #fff;
		background: #ccc;
		/* ÊµÖÁÅ∞Ëâ≤ÂúÜÂΩ¢ËÉåÊôØ */
		width: 30rpx;
		height: 30rpx;
		line-height: 28rpx;
		text-align: center;
		border-radius: 50%;
		/* ÂúÜÂΩ¢ */
		margin-right: 12rpx;
		/* ‰∏éËæìÂÖ•Ê°ÜÁöÑÈó¥Ë∑ù */
		flex-shrink: 0;
		/* Èò≤Ê≠¢Ë¢´Êå§Âéã */
	}

	/* ‚ú®Êñ∞Â¢ûÔºöÁ≠õÈÄâÊ†è‚ÄúÈáçÁΩÆ‚ÄùËÉ∂ÂõäÊåâÈíÆÊ†∑Âºè */
	.sort-item.reset-btn {
		color: #888;
